import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import LongyiNews from '@/components/LongyiNews'
import LongyiNew from '@/components/LongyiNew'
import ContactUs from '@/components/ContactUs'
import Technology from '@/components/Technology'
import Products from '@/components/Products'
import AboutLongyi from '@/components/AboutLongyi'
import Invest from '@/components/Invest'

Vue.use(Router)


var HomePage = {
	path: '/',
	component: Home,
	meta: {
	  breadcrumb: '首頁'
	},
  }
  
  var LongyiNewsPage = {
	path: '/longyi_news',
	component: LongyiNews,
	name:LongyiNewsPage,
	afterEach:() => {
	  document.body.scrollTop = 0;
	  document.documentElement.scrollTop = 0;
	},
	meta: {
	  breadcrumb: '最新消息'
	}
  }
  
  var LongyiNewPage = {
	path: '/longyi_new',
	component: LongyiNew,
	afterEach:() => {
	  document.body.scrollTop = 0;
	  document.documentElement.scrollTop = 0;
	},
	meta: {
	  breadcrumb: '最新消息'
	}
  }
  
  var ContactUsPage = {
	path: '/contact_us',
	component: ContactUs,
	afterEach:() => {
	  document.body.scrollTop = 0;
	  document.documentElement.scrollTop = 0;
	},
	meta: {
	  breadcrumb: '聯絡我們'
	}
  }
  
  var TechnologyPage = {
	path: '/technology',
	component: Technology,
	afterEach:() => {
	  document.body.scrollTop = 0;
	  document.documentElement.scrollTop = 0;
	},
	meta: {
	  breadcrumb: '核心技術'
	}
  }
  
  var ProductsPage = {
	path: '/products',
	component: Products,
	afterEach:() => {
	  document.body.scrollTop = 0;
	  document.documentElement.scrollTop = 0;
	},
	meta: {
	  breadcrumb: '產品介紹'
	}
  }
  
  var AboutLongyiPage = {
	path: '/about_longyi',
	component: AboutLongyi,
	afterEach:() => {
	  document.body.scrollTop = 0;
	  document.documentElement.scrollTop = 0;
	},
	meta: {
	  breadcrumb: '關於瀧儀'
	}
  }
  
  var InvestPage = {
	path: '/invest',
	component: Invest,
	afterEach:() => {
	  document.body.scrollTop = 0;
	  document.documentElement.scrollTop = 0;
	},
	meta: {
	  breadcrumb: '投資人專區'
	}
  }
  
  
  var routes = [
	HomePage,
	LongyiNewsPage,
	LongyiNewPage,
	ContactUsPage,
	TechnologyPage,
	ProductsPage,
	AboutLongyiPage,
	InvestPage
  ]
  
  
	const router = new Router({
	  routes
	})
export default router


